const CONST = {
  CONFIG: {
    GH_PAGES: true,
  },
  APP: {
      // react router path definition
      PATH: { 
        root_ghPages  : '/profile',      // using gh_pages + repository name
        root          : '',              // general ROOT

        home          : '/',
        expearience   : '/expearience',
      }
  },
  HOME: {
    numberMap: [
      '1994-11-10', //start life
      '2018-01-05', //start dev
      '2018-08-15' //start tooth
    ]
  }
};

export default {

  CONST,

  /**
   * get rr4 Path
   * @param {String} menu
   **/
  path: function(menu) {
    let left, right;

    left = CONST.CONFIG.GH_PAGES ? 'root_ghPages' : 'root';
    right = menu.toLocaleLowerCase();

    const PATH = {
      ...CONST.APP.PATH
    };

    let path = PATH[left] + PATH[right];
    console.log('path => ', path);
    return path;
  }
};